<template>
  <div>
    <h4>视口</h4>
    <P>视口声明&lt;meta name="viewport" content="width=device-width,initial-scale=1.0,user-scalable=no" &gt;</P>
    <p>作用：在移动浏览器中，当页面宽度超出设备，浏览器内部虚拟的一个页面容器，将页面容器缩放到设备这么大，然后展示。（目前大多数手机浏览器视口（承载页面的容器），宽度都是980）</p>
    <p>视口的宽度可以设置</p>
    <h3>内存空间</h3>
    <h4>一、堆与栈</h4>
    <P>执行上下文在逻辑上实现了堆栈。</P>
    <P>栈空间的的储存原理：先进后出，后进先出;(类比乒乓球存放)。</P>
    <p>堆空间的存取数据的方式，可以类比书架与书。书虽然整齐的放在书架上，但我们只要知道书的名字就可以很方便的取出；也像json格式中的数据，只要知道key-value就可以很轻松取出。</p>
    <h4>二、变量对象与基础数据类型</h4>
    <p>>javascript的执行上下文形成之后，会创建一个叫做变量对象的特殊对象。</p>
    <p>javascript的基础数据类型往往都会保存在变量对象中。javascript中有5中基础数据类型：undefined、Null、Boolean、Number、String。基本数据类型都是按值访问的，因此可以直接操作保存在变量中的实际值。</p>
    <h4>三、引用数据类型和堆内存</h4>
    <P>js的引用数据类型，如数组Array，它们的值得大小不是固定不变的。引用数据类型的值保存在堆内存中的对象。javascript不允许直接访问堆内存中的位置，因此不能直接操作对象的对内存空间。</P>
    <p>在操作对象时，实际上是在操作对象的引用而不是实际的对象。引用类型都是按引用访问的。引用，可以理解为保存在变量对象中的是一个地址，该地址与堆内存的实际值相关联。</p>
    <P>因此，当要访问堆内存中的引用数据类型时，实际上我们首先从变量对象中获取了该对象的地址引用，然后从堆内存中取得我们需要的数据</P>
    <h4>内存空间管理</h4>
    <p>javascript具有自动垃圾回收机制。</p>
    <h5>javascript的内存生命周期</h5>
    <ol>
      <li>分配你所需要的内存</li>
      <li>使用分配的内存</li>
      <li>不需要是将其释放，归还（null）</li>
    </ol>
    <p>当赋值null时，其实只是做了一个释放引用的操作，让原来的值失去引用，脱离执行环境，这个值会在下一次垃圾收集器执行操作时被找到并释放。</p>
    <h3>执行上下文</h3>
    <p>每当控制器转到可执行代码的时候，就会进入一个执行上下文，执行上下文可以理解为当前代码的执行环境，它会形成一个作用域。</p>
    <h4>三种运行环境：</h4>
    <ol>
      <li>全局环境：javascript运行起来首先进入该环境</li>
      <li>函数环境：当函数被调用执行时，会进入当前函数中执行代码</li>
      <li>eval</li>
    </ol>
    <P>javascript引擎以堆栈的方式处理运行它们，这个堆栈，我们称其为函数调用栈（call stack）</P>
    <P>栈底永远是全局上下文，而栈顶是当前正在执行的上下文</P>
    <h4>执行上下文总结：</h4>
    <ol>
      <li>单线程；</li>
      <li>同步执行，只有栈顶的上下文处于执行中，其他上下文需要等待；</li>
      <li>全局上下文只有唯一的一个，它在浏览器关闭时出栈；</li>
      <li>函数的执行上下文个数没有限制；</li>
      <li>每次某个函数被调用，就会有个新的执行上下文为其创建，即使是调用自身函数；</li>
    </ol>
    <h3>变量对象</h3>
    <p>一个执行上下文的生命周期可分为两个阶段：</p>
    <ol>
      <li>创建阶段：
        <p>在这个阶段中，执行上下文会分别创建变量对象，建立作用域链，以及确定this指向；</p>
      </li>
      <li>代码执行阶段：
        <p>这个阶段会完成变量赋值，函数引用，以及执行其他代码；</p>
      </li>
    </ol>
    <h3>变量对象创建的过程：</h3>
    <ol>
      <li>建立arguments对象。检查当前上下文中的参数，建立该对象下的属性与属性值</li>
      <li>检查当前上下文的函数声明。在变量对象中以函数名建立一个属性，属性值为指向该函数所在内存地址的引用。如果函数名的属性已经存在，那么该属性将会被新的引用覆盖。</li>
      <li>  检查当前上下文的变量声明，没找到一个变量声明，就在变量对象中以变量名建立属性，属性值为undefined。如果该变量名的属性已经存在，为了防止同名的函数被修改为undefined，则会直接跳过，原属性值不会被修改。</li>
    </ol>
    <p>function声明会比var声明优先级更高一点。</p>
    <h3>作用域链与闭包</h3>
    <h4>一、作用域与作用域链</h4>
    <h5>作用域</h5>
    <p>javascript中只有全局作用域和函数作用域；</p>
    <p>javascript代码的整个执行过程，分为两个阶段，代码编译阶段与代码执行阶段。编译由编译器完成，将代码翻译成可执行代码，这个阶段作用域链会确定。执行阶段由引擎完成，主要任务是执行可执行代码，执行上下文在这阶段创建。</p>
    <h5>作用域链</h5>
    <p>作用域链，是由当前环境与上层环境的一系列变量对象组成，它保证了当前执行环境对符合访问权限的变量和函数的有序访问。</p>
    <p>作用域链是由一系列变量对象组成，我们可以在这单向通道中，查询变量对象中的标识符，这样就可以访问到上一层作用域中的变量。</p>
    <h4>二、闭包</h4>
    <p>闭包与作用域息息相关；</p>
    <p>闭包是在函数执行过程中被确认的；</p>
    <p>闭包定义:假设函数A在函数B中的内部进行定义了，并且当函数A执行时，访问了函数B内部的变量对象，那么B就是一个闭包</p>
    <p>闭包会阻止垃圾回收机制；</p>
    <p>通过闭包，可以在其他的执行上下文中，访问到函数的内部变量；</p>
    <p>闭包中的作用域链并不会发生任何改变。在闭包中，能访问到的变量，仍然能在作用域链上查询到变量；</p>
    <h3>闭包的应用场景</h3>
    <ol>
      <li>延迟函数setTimeout</li>
      <li>柯里化</li>
      <li>模块</li>
    </ol>
    <h3>setTimeout与循环闭包</h3>
    <p>每一个setTimeout在执行时，会返回唯一ID,women使用时，常常用一个变量将这个唯一ID保存起来，用以传入clearTimeout()，清除定时器；</p>
    <p>页面中所有由setTimeout定义的操作，都将放在同一队列中一次执行；</p>
    <p>队列特点：先进先出；</p>
    <p>队列的执行时间：需要等待函数调用栈清空之后才开始执行；</p>
    <h3>this的理解</h3>
    <p>this的指向，是在函数被调用的时候确定的；</p>
    <p>在函数执行过程中，this一旦被确定，就不可更改了；</p>
    <h4>一、全局对象中的this</h4>
    <p>this指向window</p>
    <h4>二、函数中的this</h4>
    <p>如果调用者函数，被某一对象所拥有，那么该函数在调用时，内部的this指向该对象。如果函数独立调用，那么该函数内部的this，则指向undefined；但是在非严格模式中，当this指向undefined时，它会被自动指向全局对象；</p>
    <p>当obj在全局声明时，无论obj.c在什么地方调用，这里的this都指向全局变量，而当obj在函数环境中声明时，这个this指向undefined，在非严格模式下，会自动转向全局对象。</p>
    <h4>三、使用call、apply显示指定this</h4>
    <p>call、apply除了参数略有不同，其功能完全一样。它们的第一个参数都为this将要指向的对象；而callback、apply后面的参数，都是向将要执行的函数传递参数。其中call以一个一个的形式传递，apply以数组的形式传递。</p>
    <h4>四、构造函数与原型方法上的this</h4>
    <p>通过new操作符调用构造函数，会经历以下4个阶段</p>
    <ol>
      <li>创建一个新的对象；</li>
      <li>将构造函数的this指向这个新对象；</li>
      <li>指向构造函数的代码，为这个对象添加属性和方法；</li>
      <li>返回新对象；</li>
    </ol>
  </div>
</template>

<script>
  export default {
    name: 'note'
  }
</script>



