<template>
  <div id="app" class="container">
    <h3 class="page-header">zhengsenyu</h3>
    <div class="row">
      <div class="col-md-2">
        <button type="button" class="collapsed hidden-lg button-menu"  data-toggle="collapse" data-target="#list"
          aria-expanded="false">
          menu
        </button>
        <div class="collapse" id="list">
          <div class="list-group" data-toggle="collapse" data-target="#list">
            <router-link to="/person" class="list-group-item ">个人信息</router-link>
            <router-link to="/note" class="list-group-item">学习笔记</router-link>
            <router-link to="/article" class="list-group-item">文章收藏</router-link>
            <router-link to="/project" class="list-group-item">饿了么项目笔记</router-link>
            <!--<router-link to="/Json">json笔记</router-link>
            <router-link to="/css">css笔记</router-link>-->
          </div>
        </div>
      </div>
      <div class="col-md-10">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        flag: 'none'
      }
    },
    computed: {
      listGroup () {
        this.flag = !this.flag
      }
    },
    methods: {
      listUp () {
        let List = document.getElementById('list')
        List.style.display = this.flag
      },
      listDown () {
        let List = document.getElementById('list')
        List.style.display = 'block'
      }
    }
  }

</script>



